@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Arimo&display=swap');
:root {
    --primary-text: white;
    --elegance: rgb(39 39 39);
    --primary-theme: #a6c;
    --card-bg: #37474f;
    --primary-bg: #1c2331;
    --SpecailText : ;
    /* --card-bg: #ffffff; */
}
:root{
    --ColorP-1 : #2C3639;
    --ColorP-2 : #3F4E4F;
    --ColorP-3 : #A27B5C;
    --ColorP-4 : #DCD7C9;

}
body{
    font-family: 'Arimo', sans-serif;
    overflow-x: hidden;

}
body::selection{
    background-color: var(--ColorP-3);
    color: wheat;
}
.cursor-pointer{
    cursor: pointer;
}
.headingText{
    font-family: 'Roboto', sans-serif;
    position: relative;
    font-weight: bold;
    padding: 1vh 0;
    text-transform: capitalize;
}
.headingText::before{
    border: thin solid var(--primary-theme);
    content: " ";
    position: absolute;
    bottom: 0;
    padding: 0 3rem;
    border-width: 0.05em;
    background-color: var(--primary-theme);
    border-radius: 2vh; 
}
p{
    word-spacing: initial;
    text-align: justify;
}
.video-fluid{
    border-radius: 0.5em;
}
.list>li{
    position: relative;
    list-style: none;
    line-height: 2em;
    line-break: auto;
}
.list>li::before{
    content: '';
    position: absolute;
    border-right: 2px solid var(--primary-theme);
    border-bottom: 2px solid var(--primary-theme);
    width: 10px;
    height: 10px;
    top: calc(50%);
    left: -20px;
    transform: translateY(-50%) rotate(-45deg);
}

.user-bg{
    background-color: var(--ColorP-1);
    color: white;
}
a{
    text-decoration: none;
    color: var(--primary-theme);
}
a:hover{
    text-decoration: none;
    color: var(--primary-theme);
}
@media (max-width: 475px) {
    .carousel-item>img{
        height: 25vh!important;
    }
    
}
.content-card{
    /* background-image:linear-gradient(45deg,rgba(255, 0, 0, 0.274),rgba(0, 0, 255, 0.199),rgba(0, 128, 0, 0.26)),url(); */
    background-color: var(--ColorP-1);
    color: white;
    padding: 4vh 0;
}
.btn-userTheme{
    background-color: var(--primary-theme);

}
#bgdesk{
    background-image: url(../img/deskbg.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}